<template>
	<div id="search-result">
		<public-header title="搜索结果">
			<div slot="right" class="search-btn" @click="onSetSearch()">
				<span class="icon icon-search"></span>
			</div>
		</public-header>
		<public-footer :menufooter="menufooter" :showPreLoading="showPreLoading"></public-footer>
		<div class="content">
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh">
				<van-list
					v-model="orders.loading"
					:finished="orders.finished"
					@load="onLoad"
					:offset="100"
					:immediate-check="false"
					v-if="!orders.empty"
					class="order-list"
				>	
					<template v-for="(item, index) in orders.data">
						<order-item
							:order="item"
							:order_index='index'
							@onChangeOrderStatus='onChangeOrderStatus'
						></order-item>
					</template>
				</van-list>
				<div class="no-data" v-else>
					<img src="static/img/order_no_con.png" alt="">
					<p>暂无符合条件订单!</p>
				</div>
			</van-pull-refresh>
		</div>
	</div>
</template>

<script>
	import {mapState, mapMutations} from 'vuex'
	import publicHeader from "@/components/header"
	import publicFooter from "@/components/footer"
	import orderItem from "./components/orderItem"
	import {getOrderList, changeOrderStatus} from "./controller/order.js"
	export default {
		components: {
			publicHeader,
			publicFooter,
			orderItem
		},
		data() {
			return {
				showPreLoading: true,
				isRefresh: false,
				orders: {
					page: 1,
					psize: 15,
					finished: false,
					loading: false,
					data: []
				},
				menufooter: {}
			}
		},
		methods: {
			...mapMutations([
				'setSearch'
			]),
			onLoad(force) {
				let that = this;
				getOrderList({
					vue: that,
					force: force,
					data: {
						filter: that.filter ? that.filter.items: {},
						menufooter: 1
					}
				});
			},
			onPullDownRefresh() {
				this.onLoad(true);
			},
			onChangeOrderStatus(options) {
				options.vue = this;
				options.from = 'search';
				changeOrderStatus(options);
			},
			onSetSearch() {
				this.setSearch({
						title: '订单搜索',
						type: 'filter',
						key: 'takeout',
						url: 'plateform/order/takeout/search',
						redirctUrl: 'pages/order/searchResult'
					}
				);
				this.$router.push(this.util.getUrl({path: '/pages/common/search'}));
			}
		},
		computed: {
			...mapState([
				'filter',
			]),
		},
		mounted() {
			this.onLoad(false);
		},
		created() {
			this.query = this.$route.query;
			if(this.query && this.query.status) {
				this.status = this.query.status;
			}
		}
	}
</script>

<style>
	#search-result .van-pull-refresh{
		overflow: visible;
	}
	#search-result .search-btn{
		height: 45px;
	}
	#search-result .search-btn .icon-search{
		font-size: 24px;
	}
</style>


