<template>
	<div id="order-item">
		<div class="order-item">
			<div class="number-price flex-lr border-1px-b">
				<div class="number">#{{order.serial_sn}}</div>
				<div class="price">¥{{order.final_fee}}</div>
			</div>
			<router-link tag="div" class="order-detail" :to="util.getUrl({path: 'pages/order/detail', query: {id: order.id}})">
				<div class="detail-item buy-address flex-lr">
					<div class="left c-info">取货</div>
					<div class="right">
						<div class="store-title ellipsis">
							{{order.store.title}}
						</div>
						<div class="store-address ellipsis">
							{{order.store.address}}
						</div>
					</div>
				</div>
				<div class="detail-item accept-address flex-lr">
					<div class="left c-primary">送货</div>
					<div class="right">
						<div class="store-title">
							{{order.address}}
						</div>
						<div class="store-address">
							{{order.username}} {{order.mobile}}
						</div>
					</div>
				</div>
				<div class="detail-item flex-lr" v-if="order.deliveryer_id > 0 && order.deliveryer">
					<div class="left c-danger">配送:</div>
					<div class="right">
						<div class="store-title">
							{{order.deliveryer.title}} {{order.deliveryer.mobile}}
						</div>
						<div class="store-address" v-if="order.delivery_status == 7">
							已接单 (接单时间：{{order.delivery_assign_time_cn}})
						</div>
						<div class="store-address" v-else-if="order.delivery_status == 8">
							已到店 (到店时间：{{order.delivery_instore_time_cn}})
						</div>
						<div class="store-address" v-else-if="order.delivery_status == 4">
							已取货 (取货时间：{{order.delivery_takegoods_time_cn}})
						</div>
						<div class="store-address" v-else-if="order.delivery_status == 5">
							已送达 (送达时间：{{order.delivery_success_time_cn}})
						</div>
					</div>
				</div>
				<div class="detail-item note flex-lr">
					<div class="left">备注</div>
					<div class="right">{{order.note ? order.note : '无'}}</div>
				</div>
				<div class="detail-item flex-lr">
					<div class="left">下单</div>
					<div class="right">{{order.addtime_cn}}</div>
				</div>
				<div class="detail-item flex-lr">
					<div class="left">配送</div>
					<div class="right">{{order.delivery_day}} {{order.delivery_time}}</div>
				</div>
			</router-link>
			<ul class="btn-group border-1px-t" v-if="order.status < 5 || order.refund_status == 1 || order.refund_status == 2">
				<template v-if="order.status < 5">	
					<template v-if="order.status == 1">
						<li class="btn-item" @click="onChangeOrderStatus(order, 'notify_clerk_handle', order_index)">通知商户</li>
						<li class="btn-item btn-primary" @click="onChangeOrderStatus(order, 'handle', order_index)">接单</li>
					</template>
					<template v-if="order.order_type == 1">
						<template v-if="order.status == 2 || order.status == 3">
							<li class="btn-item" @click="onChangeOrderStatus(order, 'notify_deliveryer_collect', order_index)">通知抢单</li>
							<router-link tag="li" :to="util.getUrl({path: 'pages/order/dispatch', query: {id: order.id}})" class="btn-item" v-if="order.delivery_type == 2">调度</router-link>
							<li class="btn-item" @click="onPushUU(order.id)" v-if="order.uupaotui_status && order.delivery_type == 2">UU配送</li>
						</template>
						<template v-if="order.status == 4">
							<li class="btn-item" @click="onChangeOrderStatus(order, 're_notify_deliveryer_collect', order_index)">重新待抢</li>
							<router-link tag="li" :to="util.getUrl({path: 'pages/order/dispatch', query: {id: order.id}})" class="btn-item">重新调度</router-link>
						</template>
						<template v-if="order.status > 1">
							<li class="btn-item" @click="onChangeOrderStatus(order, 'end', order_index)">完成</li>
						</template>
					</template>
					<template v-else>
						<li class="btn-item" @click="onChangeOrderStatus(order, 'end', order_index)">完成</li>
					</template>
					<template v-if="order.is_pay == 1 && order.pay_type != 'delivery'">
						<li class="btn-item bg-danger" @click="onChangeOrderStatus(order, 'cancel_refund', order_index)">取消</li>
					</template>
					<template v-else>
						<li class="btn-item bg-danger" @click="onChangeOrderStatus(order, 'cancel', order_index)">取消</li>
					</template>
				</template>
				<template v-if="order.refund_status == 1">
					<li class="btn-item bg-danger" @click="onChangeOrderStatus(order, 'refund_handle', order_index)">发起退款</li>
					<li class="btn-item bg-danger" @click="onChangeOrderStatus(order, 'refund_status', order_index)">已退款</li>
				</template>
				<template v-else-if="order.refund_status == 2">
					<li class="btn-item bg-danger" @click="onChangeOrderStatus(order, 'refund_query', order_index)">查询退款</li>
					<li class="btn-item bg-danger" @click="onChangeOrderStatus(order, 'refund_status', order_index)">已退款</li>
				</template>
			</ul>
		</div>
	</div>
</template>

<script>
	export default {
		props: {
			order: Object,
			order_index: Number,
		},
		data() {
			return {
				isRefresh: false
			}
		},
		components: {
			
		},
		methods: {
			onChangeOrderStatus: function(order, type, index) {
				var params = {order: order, type: type, index: index};
				this.$emit('onChangeOrderStatus', params);
			},
			onPushUU(orderId) {
				this.$emit('onPushUU', {orderId: orderId});
			}
		}
	}
</script>

<style>
	#order-item .order-item{
		background-color: #fff;
		margin-bottom: 10px;
		font-size: 15px;
	}
	#order-item .order-item .number-price{
		padding: 10px;
	}
	#order-item .order-item .number-price .number{
		font-size: 20px;
		color: #E87613;
	}
	#order-item .order-item .number-price .price{
		color: #EB0000;
	}
	#order-item .order-item .order-detail{
		padding: 10px 10px 0;
	}
	#order-item .order-item .order-detail .detail-item{
		padding-bottom: 10px;
	}
	#order-item .order-item .order-detail .left{
		width: 12%;
	}
	#order-item .order-item .order-detail .right{
		width: 88%;
	}
	#order-item .order-item .order-detail .note .right{
		line-height: 1.2;
	}
	#order-item .order-item .order-detail .store-address{
		margin-top: 10px;
		font-size: 14px;
		color: #c9c9c9;
	}
	#order-item .order-item .btn-group{
		padding: 10px;
		display: flex;
		align-items: center;
		justify-content: flex-end;
	}
	#order-item .order-item .btn-group .btn-item{
		min-width: 60px;
		margin-left: 10px;
		text-align: center;
		padding: 10px 8px;
		border: 1px solid #e5e5e5;
		border-radius: 3px;
		font-size: 14px;
	}
	#order-item .order-item .btn-group .btn-item.btn-primary{
		background-color: #4FAE52;
		border-color: #4FAE52;
		color: #fff;
	}
</style>


